import React from "react"

const ImageColumn = ({ imageList }) => {
  return (
    <div className="full-width-image-container">
      {imageList.map((image) => {
        return (
          <img key={image.key} className="flex-images" src={image.image} alt={image.alt}></img>
        )
      })}
    </div>
  )
}

export default ImageColumn